<template>
  <div class="product-content" v-if='isRender'>
      <div class="product-decs">
        <p class="product-decs-top"><span class="product-price">￥<b class="product-priceNum">{{productdetail.content.priceText}}</b></span><span class="product-salecount">{{productdetail.content.sellCount}}件已售</span></p>
        <h3 class="product-decs-name">{{productdetail.content.title}}</h3>
        <p class="product-decs-bottom"><span class="product-del">{{productdetail.content.postage}}</span><span class="product-del">月销量{{productdetail.content.soldCount}}件</span><span class="product-del">{{productdetail.content.from}}</span></p>
      </div>
      <div class="product-comment">
        <p class="product-comment-top">商品评价({{productdetail.content.review.totalCount}})</p>
        <ul class="product-comment-category">
          <li class="product-comment-category-item" v-for="item in productdetail.content.review.keywords" :key='item.attribute'>{{item.word}}<span class="comment-num">({{item.count}})</span></li>
        </ul>
        <ul class="product-comment-list">
          <li class="product-comment-list-item">
            <p class="commenter-info"><img class="commenter-img" :src="productdetail.content.review.rateList[0].headPic"><span class="commenter-name">{{productdetail.content.review.rateList[0].userName}}</span></p>
            <p class="comment-content">{{productdetail.content.review.rateList[0].content}}</p>
            <p class="comment-others"><time>{{productdetail.content.review.rateList[0].dateTime}}</time><span>{{productdetail.content.review.rateList[0].skuInfo}}</span></p>
          </li>
        </ul>
      </div>
      <div class="product-store">
        <div class="product-store-top">
          <img class="product-store-img" :src="productdetail.content.seller.shopIcon">
          <p class="product-store-info"><span class="product-store-name">{{productdetail.content.seller.shopName}}</span><span class="product-store-brand">天猫</span></p>
          <span class="product-store-btn">全部宝贝</span>
          <span class="product-store-btn">进店逛逛</span>
        </div>
        <div class="product-store-bottom">
          <span v-for="item in productdetail.content.seller.evaluates" :key="item.title">{{item.title}}&nbsp;<b>{{item.score}}</b>&nbsp;<i class="myiconfont">{{item.levelText}}</i></span>
          <!-- <span>卖家服务&nbsp;<b>4.8</b></span> -->
          <!-- <span>物流服务&nbsp;<b>4.8</b></span> -->
        </div>
      </div>
  </div>
</template>

<script>
  // import {getProductDetails} from 'api/product';
  // import storage from 'assets/js/storage';
  // import {PRODUCT_CONTENT_KEYWORD_KEY} from './config';
  export default {
    name: 'ProductContent',
    data() {
      return {
        // productId: '',
        // // contents: {},
        productdetail: {},
        isRender: true
      };
    },
    props: {
      contents: {
        type: Object,
        default() { return {}; }
      }
    },
    created() {
      this.productdetail = this.contents;
      if (Object.keys(this.productdetail).length === 0) { // 若没有数据就不要进行渲染了
          this.isRender = false;
      }
    },
    methods: {
      // init() {
      //   this.productId = this.$route.params.id; // $router特有的方法
      //   this.getProductData(this.productId);
      //   if (Object.keys(this.productdetail).length === 0) { // 若没有数据就不要进行渲染了
      //     this.isRender = false;
      //   }
      // },
    //   getProductData(id) {
    //     debugger;
    //     return getProductDetails(id).then(data => {
    //       console.log(data);
    //       this.productdetail = data;
    //       storage.set(PRODUCT_CONTENT_KEYWORD_KEY, data);
    //       console.log(this.productdetail);
    //       });
    //   }
    // }
    // watch: {
    //   productId(productId) {
    //     this.getProductData(productId);
    //     // this.productdetail = storage.get(PRODUCT_CONTENT_KEYWORD_KEY, {});
    //     console.log(this.productdetail);
    //     if (Object.keys(this.productdetail).length === 0) { // 若没有数据就不要进行渲染了
    //       this.isRender = false;
    //     }
    //     this.isRender = true;
    //   }
    }
};
</script>

<style lang="scss" scoped>
  @import '~assets/scss/mixins';
  .product-decs{
    margin-bottom: 10px;
    background: #fff;
    &-top{
      display: flex;
      padding: 10px 6px;
      justify-content: space-between;
      align-items: center;
      background: rgb(201, 50, 100);
      font-size: 13px;
      color: #fff;
      .product-priceNum{
        font-size: 16px;
      }
    }
    &-name{
      padding: 12px 6px;
      line-height: 1.2;
      font-size: 15px;
      font-weight: bold;
    }
    &-bottom{
      display: flex;
      padding: 0 8px 10px;
      justify-content: space-between;
      color: #aaa;
    }
  }
  .product-comment{
    background: #fff;
    padding: 0 8px;
    margin-bottom: 10px;
    &-top{
      font-size: 15px;
      padding: 10px 0;
    }
    &-category{
      display: flex;
      flex-wrap: wrap;
      &-item{
        padding: 5px;
        margin: 0 6px 6px;
        border-radius: 10px;
        background: antiquewhite;
      }
    }
    &-list{
      &-item{
        .commenter-info{
          display: flex;
          align-items: center;
          padding-top: 6px;
          .commenter-img{
            width: 24px;
            height: 24px;
          }
        }
        .comment-content{
          padding: 8px;
          line-height: 1.2;
        }
        .comment-others{
          padding: 0 8px 8px;
          color: #aaa;
          time{
            margin-right: 6px;
          }
        }
      }
    }
  }
  .product-store{
    padding: 10px;
    margin-bottom: 30px;
    background: #fff;
    &-top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .product-store-img{
        width: 40px;
        height: 40px;
        margin-right: 8px;
      }
      .product-store-info{
        display: flex;
        flex: 1;
        flex-direction: column;
        font-size: 14px;
        .product-store-brand{
          width: 36px;
          padding: 3px 0 1px;
          border-radius: 10px;
          background: rgb(223, 54, 54);
          font-size: 10px;
          color: #fff;
          text-align: center;
        }
      }
      .product-store-btn{
        padding: 6px;
        border: 1px solid orange;
        border-radius: 16px;
      }
      .product-store-btn ~ .product-store-btn{
        color: orange;
        margin-left: 6px;
      }
      .product-store-btn + .product-store-btn{
        color: #fff;
        background: orange;
      }
    }
    &-bottom{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 0 6px;
      color: #aaa;
      .myiconfont{
        color: red;
      }
    }
  }
</style>



